@charset "UTF-8";
body {
  font-family: "Gotu", "Noto Sans JP", serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4814814815vw;
  }
}
@media (min-width: 1080px) {
  html {
    font-size: 16px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}

.layout-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 1000;
}

.common-btn {
  position: relative;
  padding: 1rem 2.5rem;
  display: flex;
  background-color: #408f95;
  transition: 0.3s;
  color: #fff;
  width: 12.625rem;
  align-items: center;
  border: 0.0625rem solid transparent;
}

.common-btn:hover {
  background-color: #fff;
  color: #408f95;
  border-color: #408f95;
}

.common-btn::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 0.0625rem solid #408f95;
  transform: translateY(0.25rem) translateX(0.25rem);
  box-sizing: border-box;
  z-index: -1;
}

.common-btn p {
  display: flex;
  align-items: center;
  font-size: 0.875rem;
  line-height: 1.5714285714;
  letter-spacing: 0;
  position: static;
}

.common-btn img {
  position: static;
  width: 2.5rem;
  height: 0.375rem;
  transform: none;
  margin-left: 0.375rem;
  margin-bottom: 0.25rem;
}

.common-btn:hover img {
  content: url("./../images/common/Vector-green.svg");
}

.common-title {
  text-align: center;
}

.common-title__main {
  font-size: 2rem;
  line-height: 1.25;
  font-weight: 400;
}
@media screen and (min-width: 768px) {
  .common-title__main {
    font-size: 2.5rem;
    line-height: 1;
  }
}

.common-title__main:first-letter {
  color: #408f95;
}

.common-title__sub {
  font-size: 0.75rem;
  line-height: 1.5;
  color: #408f95;
  margin-top: 0.25rem;
}
@media screen and (min-width: 768px) {
  .common-title__sub {
    margin-top: 0.5rem;
  }
}

.common-title__main--white:first-letter {
  color: #fff;
}

@media screen and (min-width: 768px) {
  .common-title__main--white,
  .common-title__sub--white {
    color: #fff;
  }
}

.drawer-menu {
  display: none;
  position: absolute;
  z-index: 800;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  padding: 6.5rem 0;
  background-color: #408f95;
  overflow-y: scroll;
  scrollbar-width: none;
}

.drawer-menu::-webkit-scrollbar {
  display: none;
}

.drawer-menu__wrapper {
  display: flex;
}

.drawer-menu__body:nth-child(2) {
  margin-left: 4px;
}

.drawer-menu__block {
  padding-top: 1.75rem;
}

.drawer-menu__item {
  position: relative;
  padding-left: 1.375rem;
}

.drawer-menu__item::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 1.125rem;
  height: 1.125rem;
  background-image: url(./../images/common/icon.png);
  background-size: cover;
  background-repeat: no-repeat;
}

.drawer-menu__link {
  font-size: 1rem;
  font-weight: 700;
  color: #fff;
  line-height: 1.125;
  padding-bottom: 0.75rem;
  display: block;
}

.drawer-menu__sub-item {
  padding: 0.75rem 0;
}

.drawer-menu__sub-link {
  font-size: 1rem;
  line-height: 1.125;
  color: #fff;
}

.drawer-menu__item:nth-child(2) .drawer-menu__link {
  margin-top: 1.75rem;
}

.drawer-menu__item:nth-child(n+3) {
  margin-top: 1.75rem;
}

.drawer-menu__link,
.drawer-menu__sub-link {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.hamburger {
  display: block;
  padding: 0 0.3125rem;
  outline: none;
  border: none;
  position: relative;
  z-index: 999;
  width: 2rem;
  height: inherit;
  cursor: pointer;
  transition: 0.3s;
}
@media screen and (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.hamburger span {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  width: 2rem;
  height: 0.0625rem;
  background-color: #fff;
  border-radius: 0.25rem;
  transition: 0.5s;
}

.hamburger span:nth-of-type(1) {
  top: 1.75rem;
}

.hamburger span:nth-of-type(2) {
  top: 2.5rem;
}

.hamburger span:nth-of-type(3) {
  top: 3.25rem;
}

.hamburger.is-open span:nth-of-type(1) {
  top: 2.3125rem;
  transform: translateX(-50%) rotate(45deg);
}

.hamburger.is-open span:nth-of-type(2) {
  opacity: 0;
}

.hamburger.is-open span:nth-of-type(3) {
  top: 2.1875rem;
  transform: translateX(-50%) rotate(-45deg);
}

.header {
  height: 5rem;
  background-color: rgba(13, 41, 54, 0.32);
}
@media screen and (min-width: 768px) {
  .header {
    height: 5.625rem;
  }
}

.header__inner {
  padding: 0 0.9375rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: inherit;
}
@media screen and (min-width: 768px) {
  .header__inner {
    padding: 0 1.25rem 0 2.5rem;
  }
}

.header__logo {
  max-width: 6.375rem;
  width: 100%;
  height: inherit;
  z-index: 1000;
}
@media screen and (min-width: 768px) {
  .header__logo {
    max-width: 8.3125rem;
  }
}

.header__logo a {
  height: inherit;
  display: flex;
  align-items: center;
}

.header__logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header__nav {
  height: inherit;
}

.header__nav-list {
  display: flex;
  height: inherit;
}

.header__nav-item {
  height: inherit;
}

.header__nav-item a {
  height: inherit;
  display: flex;
  align-items: center;
  padding: 0 1.25rem;
  text-align: center;
}

.header__nav-item-text--main {
  font-size: 1.125rem;
  line-height: 1;
  color: #fff;
}

.header__nav-item-text--sub {
  font-size: 0.625rem;
  line-height: 1.5;
  color: #fff;
  margin-top: 0.25rem;
}

.header__nav-item-text--main,
.header__nav-item-text--sub {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.inner {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1130px;
    padding-right: 25px;
    padding-left: 25px;
  }
}